<template lang="pug">
  div#videoEmbed
    div.container
      h1.easy__text--title.section__title
        |     
        br
        |     

      youtube.video__body(
        video-id="_62t0IooHYw"
      )
      p.videoEmbed__from
        span.videoEmbed__from--text :
          a.videoEmbed__from--link(
            :href="videoLink"
            target="_blank"
            v-tooltip.bottom="{content: videoChannel + '   ( )', delay: {show: 500, hide: 100}}"
          ) &nbsp;{{ videoChannel }}&nbsp;
            i.videoEmbed__from--icon.fab.fa-youtube
</template>

<script>
import Vue from 'vue'
import VueYoutube from 'vue-youtube'
import { globalVar } from '@/globalVar'

Vue.use(VueYoutube)

export default {
  name: 'videoEmbed',

  data: _ => ({
    videoChannel: ' ',
    videoId: process.env.YT_ID,
    serviceKo: globalVar.serviceKo,
    videoLink: 'https://www.youtube.com/channel/UCoeXIHBxfJ7ahvMV4y8a9wQ',
  }),

}
</script>



